import { FETCH_RANDOM_RHYTHM, VOTE_FOR_RHYTHM } from './constants.js';
import { createPlayerReducer } from '~/modules/audio/reducer';

const RhythmPlayerReducer = createPlayerReducer('RandomRhythm');

const initialState = {
  ...RhythmPlayerReducer(undefined, {}),
  loading: false,
  voting: false
};
export default function rhythmReducer(state = initialState, action){
  switch(action.type) {
    case FETCH_RANDOM_RHYTHM+'_PENDING':
      return {
	  ...state,
	  loading: true
      }
    case FETCH_RANDOM_RHYTHM+'_FULFILLED':
      return {
	  ...state,
	  loading: false,
	  error: undefined,
	  data: action.payload
      }
    case FETCH_RANDOM_RHYTHM+'_REJECTED':
      return {
	  ...state,
	  loading: false,
	  error: action.payload
      }
    case VOTE_FOR_RHYTHM+'_PENDING':
      return {
	  ...state,
	  voting: true
      }
    case VOTE_FOR_RHYTHM+'_FULFILLED':
      return {
	  ...state,
	  voting: false,
	  error: undefined,
	  vote: action.payload
      }
    case VOTE_FOR_RHYTHM+'_REJECTED':
      return {
	  ...state,
	  voting: false,
	  error: action.payload
      }
    default:
      return RhythmPlayerReducer(state, action);
  }
}
