import instance from '~/api/axiosconfig';
import { FETCH_RANDOM_RHYTHM,VOTE_FOR_RHYTHM } from './constants';
import { createPlayerActions } from '~/modules/audio/actions';

export const fetchRandomRhythm = () => ({
    type: FETCH_RANDOM_RHYTHM,
    payload: instance.get('/rhythm/random', {
      headers: {'Accept': 'application/json'}
    }).then(result => result.data)
});

export const {
    start: RandomRhythmStart,
    stop: RandomRhythmStop,
    setVolume: RandomRhythmSetVolume
  } = createPlayerActions('RandomRhythm');

const sendVoteForRhythm = (uniqueId, vote, atDuration) => ({
    type: VOTE_FOR_RHYTHM,
    payload: instance.post('/vote/for/rhythm/'+uniqueId, {
      vote,
      atDuration,
      remark: ''
    }, {
      headers: {'Accept': 'application/json'}
    }).then(result => result.data)
});
export const voteForRhythm = (uniqueId, vote, atDuration) => {
    return (dispatch) => {
        dispatch(RandomRhythmStop());
        dispatch(sendVoteForRhythm(uniqueId, vote, atDuration));
        const fetchRandomRhythmAction = fetchRandomRhythm();
        dispatch(fetchRandomRhythmAction);
        fetchRandomRhythmAction.payload.then((result) => {
          dispatch(RandomRhythmStart());
          return result;
        });
    }
};
