import { PRELOAD_SOUNDS } from './constants.js';

export default function audioReducer(state = {}, action){
  switch(action.type) {
    case PRELOAD_SOUNDS+'_PENDING':
      return {
	  ...state,
	  loading: true
      }
    case PRELOAD_SOUNDS+'_FULFILLED':
      return {
	  ...state,
	  loading: false,
	  error: undefined,
	  data: action.payload
      }
    case PRELOAD_SOUNDS+'_REJECTED':
      return {
	  ...state,
	  loading: false,
	  error: action.payload
      }
    default:
      return state;
  }
}

const initialPlayerState = {
  play: false,
  volume: 0.1
};
export const createPlayerReducer = BASE => { return (state = initialPlayerState, action) => {
  switch(action.type) {
    case BASE+'_PLAY':
      return {
	  ...state,
	  play: true
      }
    case BASE+'_STOP':
      return {
	  ...state,
	  play: false
      }
    case BASE+'_SET_VOLUME':
      return {
	  ...state,
	  volume: max(0, min(1, action.payload))
      }
    default:
      return state;
  }
}};
