const selectRhythm = state => state.rhythm;

export const needsLoading = state => typeof selectRhythm(state).data === 'undefined';
export const getData = state => selectRhythm(state).data || {};
export const isLoading = state => selectRhythm(state).loading;
export const getError = state => selectRhythm(state).error;

export const isVoting = state => selectRhythm(state).voting;
export const getVote = state => selectRhythm(state).vote;

export const getTempo = state => needsLoading(state) ? undefined : getData(state).tempo;
export const getKick = state => needsLoading(state) ? undefined : getData(state).kick;
export const getSnare = state => needsLoading(state) ? undefined : getData(state).snare;
export const getHihat = state => needsLoading(state) ? undefined : getData(state).hihat;
export const getLength = state => needsLoading(state) ? undefined : getData(state).length;
export const getUniqueId = state => needsLoading(state) ? undefined : getData(state).uniqueId;
