import instance from 'axios';
import { PRELOAD_SOUNDS } from './constants';
import createAudioContext from './createAudioContext';

const kick = require('~/sounds/kick.wav');
const snare = require('~/sounds/snare.wav');
const hihat = require('~/sounds/hihat.wav');
const list = {
  kick,
  snare,
  hihat,
};
const context = createAudioContext();

const map = (myObject, func) => Object.keys(myObject).reduce(function(previous, current) {
    previous[current] = func(myObject[current]).then(result => {previous[current] = result;});
    return previous;
}, {});

const load = url => instance.get(url, { responseType: 'arraybuffer' }).then(result => result.data);

const loadAndDecode = (list, context) => {
  const sounds = map(list, url => load(url).then(response => new Promise((resolve, reject) => {
    context.decodeAudioData(response, resolve, reject)
  })));
  return instance.all(Object.values(sounds)).then(() => sounds);
};

export const preloadSounds = () => ({
    type: PRELOAD_SOUNDS,
    payload: loadAndDecode(list, context),
})

export const createPlayerActions = BASE => ({
  start: () => ({
    type: BASE+'_PLAY'
  }),
  stop: () => ({
    type: BASE+'_STOP'
  }),
  setVolume: (volume) => ({
    type: BASE+'_SET_VOLUME',
    payload: volume
  }),
});
