import React, { Component } from 'react';
import { Container, Button, Header, Loader, Message, Input } from 'semantic-ui-react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { fetchRandomRhythm, RandomRhythmStart, RandomRhythmStop, voteForRhythm } from './actions';
import { needsLoading, getData, isLoading, getError, getUniqueId, isVoting } from './selectors';
import RhythmPlayer from './RhythmPlayer';

class RandomRhythm extends Component {
  static propTypes = {
    error: PropTypes.instanceOf(Error),
    needsLoading: PropTypes.bool.isRequired,
    loading: PropTypes.bool.isRequired,
    data: PropTypes.object.isRequired,
    fetchRandomRhythm: PropTypes.func.isRequired,
    voteForRhythm: PropTypes.func.isRequired,
    voting: PropTypes.bool.isRequired,
    startPlay: PropTypes.func.isRequired,
    stopPlay: PropTypes.func.isRequired,
  };

  state = {
    volume: 10
  }

  componentDidMount() {
    this.props.needsLoading && this.props.fetchRandomRhythm();
    this.playerRef = React.createRef();
  }

  onVoteClicked(vote) {
    const currentTime = this.playerRef.current.getWrappedInstance().getCurrentTime();
    this.props.voteForRhythm(this.props.data.uniqueId, vote, currentTime);
  }

  handleOnChange = (event, data) => {
    this.setState({
      volume: data.value
    })
  }

  render() {
    const { needsLoading, loading, voting } = this.props;
    const { volume } = this.state;
    return (
      <Container>
        <Header as="h2">Random Rhythm</Header>

	<Message error hidden={!this.props.error}>{this.props.error ? this.props.error.message : null}</Message>

	{!needsLoading && <RhythmPlayer rhythm={this.props.data} play={this.props.play} volume={volume/100} ref={this.playerRef} />}
	<Input
	  defaultValue={volume}
	  type="range"
	  onChange={this.handleOnChange}
	/>

	{!this.props.play && <Button onClick={this.props.startPlay}>Play</Button>}
	{this.props.play && <Button onClick={this.props.stopPlay}>Stop</Button>}
	<Button.Group icon>
	  <Button onClick={() => this.onVoteClicked(4)} disabled={loading||voting}>😀</Button>
	  <Button onClick={() => this.onVoteClicked(3)} disabled={loading||voting}>😊</Button>
	  <Button onClick={() => this.onVoteClicked(2)} disabled={loading||voting}>😐</Button>
	  <Button onClick={() => this.onVoteClicked(1)} disabled={loading||voting}>☹️</Button>
	  <Button onClick={() => this.onVoteClicked(0)} disabled={loading||voting}>😞</Button>
	</Button.Group>

      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    data: getData(state),
    error: getError(state),
    loading: isLoading(state),
    needsLoading: needsLoading(state),
    play: state.rhythm.play,
    voting: isVoting(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchRandomRhythm: () => dispatch(fetchRandomRhythm()),
    voteForRhythm: (uniqueId, vote, currentTime) => dispatch(voteForRhythm(uniqueId, vote, currentTime)),
    startPlay: () => dispatch(RandomRhythmStart()),
    stopPlay: () => dispatch(RandomRhythmStop()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RandomRhythm)
