import React, { Component } from 'react';
import { Container, Button, Header, Loader, Message, List } from 'semantic-ui-react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { needsLoading, getKick, getSnare, getHihat } from '~/modules/audio/selectors';
import { preloadSounds } from '~/modules/audio/actions';
import Player from '~/modules/audio/Player';

class RhythmPlayer extends Component {
  static propTypes = {
    rhythm: PropTypes.object.isRequired,
    needsLoading: PropTypes.bool.isRequired,
    preloadSounds: PropTypes.func.isRequired,
    sounds: PropTypes.objectOf(PropTypes.instanceOf(AudioBuffer)).isRequired,
  };

  componentDidMount() {
    this.startTime = undefined;
    this.props.needsLoading && this.props.preloadSounds();
    this.audioContext = new AudioContext();
    this.player = new Player(this.audioContext, this.props.volume);

    if (this.props.play) this.playSound();
  }

  componentWillUnmount() {
    this.stopSound();
  }

  componentDidUpdate(prevProps) {
    if (this.props.needsLoading) {
      return;
    }

    if (!prevProps.play && this.props.play) this.playSound();
    if (prevProps.play && !this.props.play) this.stopSound();

    if (prevProps.volume !== this.props.volume) {
      this.player.setVolume(this.props.volume);
    }
  }

  playSound() {
    // We'll start playing the rhythm 100 milliseconds from "now"
    this.startTime = this.audioContext.currentTime + 0.100;
    this.eighthNoteTime = (60 / this.props.rhythm.tempo) / 2;
    this.bar = 0;
    this.output = this.audioContext.createGain();
    this.output.connect(this.player.getOutput());
    this.audioContext.resume();

    this.addBar();
    this.interval = setInterval(this.addBar, this.props.rhythm.length * this.eighthNoteTime * 1000);
  }

  addSound = (buffer, time) => {
    const sound = this.audioContext.createBufferSource();
    sound.buffer = buffer;
    sound.connect(this.output);
    if (!sound.start)
      sound.start = sound.noteOn;
    sound.start(time);
  };

  addInstrumentToBar = (sound, times, bar) => {
    const time = this.startTime + bar * this.props.rhythm.length * this.eighthNoteTime;
    for (var i = 0; i < this.props.rhythm.length; ++i) {
      if (times & 1) {
	this.addSound(sound, time + i * this.eighthNoteTime);
      }
      times >>= 1;
    }
  };

  addBar = () => {
    const time = this.audioContext.currentTime - (this.startTime + this.bar * this.props.rhythm.length * this.eighthNoteTime);
    console.log(this.bar, time, this.startTime, this.audioContext.currentTime);
    if (time < -10) {
      return;
    }

    for (let name in this.props.sounds) {
      const sound = this.props.sounds[name];
      if (name in this.props.rhythm) {
        this.addInstrumentToBar(sound, this.props.rhythm[name], this.bar);
      }
    }

    this.bar++;
    if (time > -0.05) {
      // account for drift
      this.addBar();
    }
  };

  stopSound = () => {
    this.output.disconnect(this.player.getOutput());
    this.audioContext.suspend();
    if (this.interval) {
      clearInterval(this.interval);
    }
    this.interval = undefined;
  };

  render() {
    return (
    <React.Fragment>
      {!this.props.needsLoading && <Message>{this.props.rhythm.uniqueId}</Message>}
    </React.Fragment>
    );
  }

  getCurrentTime() {
    return this.audioContext.currentTime - this.startTime;
  }
}

const mapStateToProps = (state) => {
  return {
    sounds: {
      kick: getKick(state),
      snare: getSnare(state),
      hihat: getHihat(state),
    },
    needsLoading: needsLoading(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    preloadSounds: () => dispatch(preloadSounds()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps, null, { withRef: true })(RhythmPlayer)
